.footer-area{
    .footer-pera{
        p{
            color:#5E5E5E;
            font-size: 16px;
            margin-bottom: 21px;
            line-height: 1.8;
        }
        &.footer-pera2 p{
            padding: 0;
        }
    }
    .footer-tittle{
        h4{
            color: $heading-color;
            font-size: 20px;
            margin-bottom: 40px;
            font-weight: 600;
        }
        ul{
            li{
                margin-bottom: 15px;
                 a{
                    color: #5E5E5E;
                    font-weight: 400;
                    font-size: 16px;
                    text-decoration: underline transparent;
                    &:hover{
                        color:#5E5E5E;
                        text-decoration: underline;
                    }
                }
                span{
                    color: red;
                }
            }
        }
        p{
            color: #5E5E5E;
            font-size: 16px;
        }
    }
    .footer-form{
        margin-top: 40px;
         form{
            position: relative;
             input{
                width: 100%;
                height: 56px;
                padding: 10px 20px;
                border: 1px solid $theme-color;
                color: #5E5E5E;
                background: #fff;
                border-radius: 30px;
                &::placeholder{
                    color: #5E5E5E;
                    font-size: 16px;
                }
            }
            .form-icon{
                button{
                    position: absolute;
                    top: 0;
                    right: 0;
                    background: none;
                    border: 0;
                    cursor: pointer;
                    padding: 20px 22px;
                    // background:$theme-color;
                    line-height: 1;
                    border-radius: 0 3px 3px 0;
                }
            }
        }
    }
    .info.error {
        color:  $theme-color;
    }
}

//copy Right 
.footer-bottom-area {
    background:#F9FAFB;
    .footer-border{
        margin-top: 60px;
        padding-bottom: 1px;
        padding-top: 19px;
        @media #{$md}{
            margin-top:0;
        }
        @media #{$sm}{
            margin-top:0;
        }
        @media #{$xs}{
            margin-top:0;
        }
    }
    .footer-copy-right{
        p{
            color: #5E5E5E;
            font-weight: 400;
            font-size: 14px;
            line-height: 2;
            i{
                color: $theme-color;
            }
            a{
                color: $theme-color;
                &:hover{
                    // color: #C2C5DB;
                }
            }
        }
    }
}

// footer social
.footer-social{
    li{
        display: inline-block;
        a{
            color: #BCBCBC !important;
            font-size: 24px !important;
            display: inline-block;
            text-align: center;
            padding: 10px;
            i{
                @include transition(.4s);
                display:inline-block;
                transform: rotateY(0deg);
                -webkit-transform: rotateY(0deg);
                -moz-transform: rotateY(0deg);
                -ms-transform: rotateY(0deg);
                -o-transform: rotateY(0deg)
            }
    
            &:hover{
                color:$theme-color;
                i{
                    transform: rotateY(180deg);
                    -webkit-transform: rotateY(180deg);
                    -moz-transform: rotateY(180deg);
                    -ms-transform: rotateY(180deg);
                    -o-transform: rotateY(180deg); 
                }
            }
    
        }
    }
    
}
.number a {
    font-family: $font_2;
	font-size: 22px !important;
    color:#5F626D !important;
    margin-bottom: 0px;
    font-weight: 600 !important;
    display: block;
    text-decoration: none !important;
    @media #{$md}{
        font-size: 19px !important;
    }
}
.number2 a {
    font-family: $font_1;
	font-size: 14px !important;
	color: #1A1A1A !important;
    margin-bottom: 5px;
    text-decoration: underline !important;
    display: block;
    @media #{$md}{
        font-size: 19px !important;
    }
}
